* {
    box-sizing: border-box;
}
body {
    font-family: "microsoft yahei", simhei;
    background-color: #10141D;
}
a {
    text-decoration: none;
}
p {
    margin: 0;
}
h5 {
    font-weight: 100;
}
hr {
    border: none;
    height: 1px;
    background-color: #636871;
}
thead {
    display: table-header-group;
    vertical-align: middle;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}
h4 {
    font-weight: 100;
}
.hide {
    display: none;
}
.report-header {
    box-shadow: 0 0 10px #E0E2E8;
    box-sizing: border-box;
    width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 9999;
    height: 78px;
    background: #0089b1
}
.header-con {
    height: 100%;
    position: relative;
}
.report-align {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
}
.content-default {
    width: 1200px;
    margin: 0 auto;
}
.report-link {
    display: block;
    right: 0;
    width: 120px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    border: 1px solid;
    border-radius: 50px;
    color: #fff;
    font-size: 14px;
}
.report-link::before {
    display: inline-block;
    content: "";
    width: 15px;
    height: 16px;
    vertical-align: text-top;
    background: url("../imgs/download.png");
}
.bg-container {
    width: 100%;
    height: 699px;
    background: url(../imgs/head_bg.jpg)50% no-repeat;
    margin-top: 78px;
}
.container {
    color: #fff;
    position: absolute;
    top: 158px;
    left: 0;
    right: 0;
}
.content-main {
    background: url("../imgs/content_bg.jpg")repeat-y;
    margin-top: -4px;
}
.part {
    box-sizing: border-box;
    padding: 38px;
}
.part+.part {
    padding-top: 0;
}
.first-part {
    padding: 0;
}
.first-part>li {
    display: inline-block;
    vertical-align: top;
    width: 24%;
}
.title-container {
    position: relative;
}
.number {
    font-size: 36px;
    font-style: italic;
    display: block;
    position: absolute;
    top: -22px;
}
.number::before {
    content: "";
    height: 1px;
    width: 40px;
    border-bottom: 1px dashed #fff;
    transform-origin: bottom center;
    transform: rotate(135deg) scale(1.414);
    position: absolute;
    /* right: 25px; */
    left: 1px;
    top: 39px;
}
.part-title {
    position: absolute;
    font-size: 29px;
    font-style: italic;
    font-weight: 100;
    top: -8px;
    left: 17px;
}
.part-content {
    height: 48px;
    box-sizing: border-box;
    border-left: 1px solid;
    padding-left: 18px;
}
.content-title {
    font-size: 14px;
}
.content-web {
    margin-top: 5px;
    font-size: 24px;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.content-web span+span {
    margin-left: 15px;
}
.deco {
    border-bottom: 1px dashed #fff;
}
.total-detail>div {
    display: inline-block;
    vertical-align: top;
}

/********************************/

.dashboard-data {
    width: 632px;
    height: 288px;
    display: block;
}
.total, .info {
    width: 310px;
    height: 138px;
    float: left;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    -o-border-radius: 4px;
    overflow: hidden;
}
.low-risk, .intermediate-risk, .high-risk, .owasp {
    width: 150px;
    height: 138px;
    float: left;
    margin-left: 8px;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px;
    -o-border-radius: 4px;
    overflow: hidden;
}
.total {
    border: 1px #616770 solid;
    background: none;
    margin-bottom: 8px;
    color: #ffffff;
}
.low-risk {
    border: 1px #3f4d08 solid;
    margin-bottom: 8px;
    color: #333333;
    background: #9fe30c;
    background: -moz-linear-gradient(top, #c8f713 0%, #7ed405 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #c8f713), color-stop(100%, #7ed405));
    background: -webkit-linear-gradient(top, #c8f713 0%, #7ed405 100%);
    background: -o-linear-gradient(top, #c8f713 0%, #7ed405 100%);
    background: -ms-linear-gradient(top, #c8f713 0%, #7ed405 100%);
    background: linear-gradient(to bottom, #c8f713 0%, #7ed405 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#c8f713', endColorstr='#7ed405', GradientType=0);
}
.intermediate-risk {
    border: 1px #dfae26 solid;
    margin-bottom: 8px;
    color: #333333;
    background: #eec728;
    background: -moz-linear-gradient(top, #edc82c 0%, #f8bf0e 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #edc82c), color-stop(100%, #f8bf0e));
    background: -webkit-linear-gradient(top, #edc82c 0%, #f8bf0e 100%);
    background: -o-linear-gradient(top, #edc82c 0%, #f8bf0e 100%);
    background: -ms-linear-gradient(top, #edc82c 0%, #f8bf0e 100%);
    background: linear-gradient(to bottom, #edc82c 0%, #f8bf0e 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#edc82c', endColorstr='#f8bf0e', GradientType=0);
}
.info {
    border: 1px #0f63a2 solid;
    color: #ffffff;
    background: #0a9be7;
    background: -moz-linear-gradient(top, #12c8fd 0%, #0166ce 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #12c8fd), color-stop(100%, #0166ce));
    background: -webkit-linear-gradient(top, #12c8fd 0%, #0166ce 100%);
    background: -o-linear-gradient(top, #12c8fd 0%, #0166ce 100%);
    background: -ms-linear-gradient(top, #12c8fd 0%, #0166ce 100%);
    background: linear-gradient(to bottom, #12c8fd 0%, #0166ce 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#12c8fd', endColorstr='#0166ce', GradientType=0);
}
.high-risk {
    border: 1px #933042 solid;
    color: #ffffff;
    background: #d61b38;
    background: -moz-linear-gradient(top, #f51e21 0%, #af1956 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #f51e21), color-stop(100%, #af1956));
    background: -webkit-linear-gradient(top, #f51e21 0%, #af1956 100%);
    background: -o-linear-gradient(top, #f51e21 0%, #af1956 100%);
    background: -ms-linear-gradient(top, #f51e21 0%, #af1956 100%);
    background: linear-gradient(to bottom, #f51e21 0%, #af1956 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#f51e21', endColorstr='#af1956', GradientType=0);
}
.owasp {
    border: 1px #933c71 solid;
    color: #ffffff;
    background: #d61893;
    background: -moz-linear-gradient(top, #f51c8d 0%, #af1956 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #f51c8d), color-stop(100%, #af1956));
    background: -webkit-linear-gradient(top, #f51c8d 0%, #af1956 100%);
    background: -o-linear-gradient(top, #f51c8d 0%, #af1956 100%);
    background: -ms-linear-gradient(top, #f51c8d 0%, #af1956 100%);
    background: linear-gradient(to bottom, #f51c8d 0%, #af1956 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#f51c8d', endColorstr='#af1956', GradientType=0);
}
:root .low-risk {
    filter: none;
}
:root .intermediate-risk {
    filter: none;
}
:root .info {
    filter: none;
}
:root .high-risk {
    filter: none;
}
:root .owasp {
    filter: none;
}
.total-title {
    float: right;
    font-size: 22px;
    height: 28px;
    line-height: 28px;
    margin-top: 88px;
}
.total-data {
    float: right;
    font-size: 120px;
    height: 138px;
    line-height: 138px;
    letter-spacing: -6px;
    padding: 0px 14px 0px 0px;
}
.title-ch {
    width: 80%;
    padding: 18px 12px 14px 0px;
    font-size: 16px;
    float: right;
    text-align: right;
    font-weight: light;
}
.data {
    width: 100%;
    float: left;
    height: 76px;
    overflow: hidden;
}
.title-en {
    text-align: right;
    font-size: 12px;
    float: right;
    margin-top: 40px;
    padding: 0px 2px 0px 0px;
    line-height: 12px;
}
.data-inner {
    font-size: 64px;
    float: right;
    height: 76px;
    line-height: 76px;
    padding: 0px 10px 0px 0px;
    font-weight: light;
    letter-spacing: -6px;
}

