.btn-default {
    padding: 11px 16px;
    border-radius: 7px;
    border: 1px solid #636871;
    background-color: transparent;
    color: #fff;
    font-family: "microsoft yahei";
}
.report-table {
    width: 100%;
    max-width: 100%;
    margin-top: 20px;
}
.report-table>thead>tr>th, .report-table>tbody>tr>th, .report-table>thead>tr>td, .report-table>tbody>tr>td, .panel-head, .panel-body {
    padding: 13px;
    font-size: 14px;
    font-weight: 100;
}
.report-table>thead>tr>td, .report-table>tbody>tr>td {
    text-align: center;
}
.report-table>thead>tr>th:first-child, .report-table>tbody>tr>td:first-child {
    text-align: left;
}
.td-adjust {
    padding-left: 20px;
}
.dan-default {
    display: inline-block;
    position: relative;
}
.dan-default::before {
    content: "";
    display: block;
    height: 18px;
    width: 18px;
    border-radius: 50%;
