}
.panel:nth-child(even)>.panel-head-spread {
    background: -webkit-linear-gradient(left, #008bb1, #03bab9);
    background: -moz-linear-gradient(left, #008bb1, #03bab9);
}
.panel:nth-child(odd)>.panel-head-spread {
    background: -webkit-linear-gradient(left, #c62367, #ea908b);
